<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen>
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
            <el-row :gutter="15">
                <el-col :span="6">
                    <el-form-item label="涉及命案" prop="murcaseId">
                        <el-select placeholder="请选择涉及命案" clearable v-model="dialogForm.murcaseId" @change="handleChangeCode">
                            <el-option v-for="(item, index) in caseListOption" :label="item.name" :value="item.id" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="dialogForm.name" placeholder="请填写姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="民族" prop="nation">
                        <el-select placeholder="请选择民族" clearable v-model="dialogForm.nation">
                            <el-option v-for="(item, index) in dic['民族']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>  
                    <el-form-item label="学历" prop="education">
                        <el-select placeholder="请选择学历" clearable v-model="dialogForm.education">
                            <el-option v-for="(item, index) in dic['学历']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="服务处所" prop="servicePlace">
                        <el-input v-model="dialogForm.servicePlace" :maxlength="25" placeholder="请填写服务处所"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                     <el-form-item label="命案编号" prop="caseNo">
                        <el-input v-model="dialogForm.caseNo" placeholder="请输入命案编号"></el-input>
                    </el-form-item>
                    <el-form-item label="证件类型" prop="cardCode">
                        <el-select placeholder="请选择证件类型" clearable v-model="dialogForm.cardCode">
                            <el-option v-for="(item, index) in dic['证件代码']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="性别" prop="sex">
                        <el-select placeholder="请选择性别" clearable v-model="dialogForm.sex">
                            <el-option v-for="(item, index) in dic['性别']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="籍贯" prop="nativePlace">
                        <el-select placeholder="请选择籍贯" clearable v-model="dialogForm.nativePlace">
                            <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                       
                        </el-select>
                    </el-form-item>
                    <el-form-item label="宗教信仰" prop="religiousBelief">
                        <el-select placeholder="请选择宗教信仰" clearable v-model="dialogForm.religiousBelief">
                            <el-option v-for="(item, index) in dic['宗教信仰']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    
                </el-col>
                <el-col :span="6">
                    <el-form-item label="证件号码" prop="cardNumber">
                        <el-input v-model="dialogForm.cardNumber" placeholder="请填写证件号码"></el-input>
                    </el-form-item>
                    <el-form-item label="出生日期" prop="birthDate">
                        <el-date-picker v-model="dialogForm.birthDate" type="date" placeholder="请选择日期"></el-date-picker>
                    </el-form-item>  
                    <el-form-item label="婚姻状况" prop="marriage">
                        <el-select placeholder="请选择婚姻状况" clearable v-model="dialogForm.marriage">
                            <el-option v-for="(item, index) in dic['婚姻状况']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>  
                    <el-form-item label="职业类别" prop="occupationType">
                        <el-select placeholder="请选择职业类别" clearable v-model="dialogForm.occupationType">
                            <el-option v-for="(item, index) in dic['职业类别']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>  
                    <el-form-item label="是否为未成年人" prop="isNonage">
                        <el-radio-group v-model="dialogForm.isNonage">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="曾用名">
                        <el-input v-model="dialogForm.oldName" :maxlength="12" placeholder="请填写曾用名"></el-input>
                    </el-form-item>
                    <el-form-item label="国籍(地区)" prop="nationality">
                        <el-select placeholder="请选择国籍(地区)" clearable v-model="dialogForm.nationality">
                            <el-option v-for="(item, index) in dic['国籍']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>  
                    <el-form-item label="政治面貌" prop="politicsStatus">
                        <el-select placeholder="请选择政治面貌" clearable v-model="dialogForm.politicsStatus">
                            <el-option v-for="(item, index) in dic['政治面貌']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="职业" prop="occupationDesc">
                        <el-input v-model="dialogForm.occupationDesc" placeholder="请填写职业"></el-input>
                    </el-form-item>
                    <el-form-item label="是否为青少年" prop="isTeenager">
                        <el-radio-group v-model="dialogForm.isTeenager">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="15">
                <el-col :span="6">
                    <el-form-item label="是否为严重精神病患者" prop="isInsanity" label-width="160px">
                        <el-radio-group v-model="dialogForm.isInsanity">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="15">
                <el-col :span="6">
                    <el-form-item label="户籍地" prop="domicilePlace">
                        <el-select placeholder="请选择户籍地" clearable v-model="dialogForm.domicilePlace">
                            <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>
                            
                        </el-select>
                    </el-form-item>  
                    <el-form-item label="居住地" prop="livePlace">
                        <el-select placeholder="请选择居住地" clearable v-model="dialogForm.livePlace">
                            <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>
                            
                        </el-select>
                    </el-form-item> 
                </el-col>
                <el-col :span="18">
                    <el-form-item label="户籍地详细地址" prop="domicilePlaceDatail">
                        <el-input v-model="dialogForm.domicilePlaceDatail" placeholder="请填写户籍地详细地址"></el-input>
                    </el-form-item>
                    <el-form-item label="居住地详细地址" prop="livePlaceDetail">
                        <el-input v-model="dialogForm.livePlaceDetail" placeholder="请填写居住地详细地址"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/district_check_service.js";

export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        证件代码: [],
        性别: [],
        国籍: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        职业类别: [],
        行政区划: []
      },
      caseListOption: [],
      disCodeOption: [],
      dialogForm: {
        isInsanity: 0,
        isNonage: 0,
        isTeenager: 0,
        districtCode: "",
        nation: "01",
        cardCode: "111"
      },
      teamRules: {
        caseNo: [
          { required: true, message: "请输入命案编号", trigger: "blur" }
        ],
        cardCode: [
          { required: true, message: "请选择证件类型", trigger: "change" }
        ],
        cardNumber: [
          { required: true, message: "请输入证件号码", trigger: "blur" },
          { min: 2, max: 30, message: "长度在 2 到 30 个字符", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 20, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        birthDate: [
          { required: true, message: "请选择出生日期", trigger: "change" }
        ],
        domicilePlace: [
          { required: true, message: "请选择户籍地", trigger: "change" }
        ]
      }
    };
  },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
    
  },
  watch: {
        "dialogForm.districtCode": {
            handler(code) {
                if (code) {
                    service.requestCodeByAddress(code, data => {
                        if (data.belongname) {
                            this.dialogForm.happenPlace =
                                data.belongname + data.name
                        } else {
                            this.dialogForm.happenPlace = "铜川市"
                        }
                    })
                }
            }
        }
    },

  methods: {
    handleChangeCode(id) {
      if (id) {
        let code = this.caseListOption.find(item => item.id == id).districtCode;
        // 根据选中项重新获取区域编码
        this.dialogForm.districtCode = code;
      }
    },
    /** 保存 */
    submitForm() {
      console.log(this.dialogForm);
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.birthDate = util.time.formatDate(param.birthDate, "YYYY-MM-DD");
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加 命案犯罪嫌疑人
            service.requestSaveSuspect(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "命案犯罪嫌疑人添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else {
            service.requestUpdateSuspect(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "命案犯罪嫌疑人修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {
        isInsanity: 0,
        isNonage: 0,
        isTeenager: 0,
        districtCode: "",
        nation: "01",
        cardCode: "111"
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>