<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :recordCount="recordCount" :disCodeOption="disCodeOption" @onAdd="handleAdd"
            @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
            @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">
                    <el-col :span="11">
                        <span class="techo-list__header-col-txt-4">姓名：</span>
                        <div class="techo-list__header-col-left-4">
                        <el-input placeholder="请输入姓名" :maxlength="10" size="small" class="techo-list__header-select-full"
                            clearable v-model="queryForm.name"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                      <span class="techo-list__header-col-txt-4">涉及命案：</span>
                        <div class="techo-list__header-col-left-4">
                            <el-select placeholder="请选择涉及命案" size="small" clearable filterable v-model="queryForm.murcaseId">
                                <el-option v-for="(item, index) in caseListOption" :label="item.name" :value="item.id" :key="index"></el-option>
                            </el-select>
                        </div>
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/district_check_service.js";
import vAddDialog from "./mur-suspect-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "命案犯罪嫌疑人",
      caseListOption: [],
      disCodeOption: [],
      queryForm: {
        districtCode: "",
        name: "",
        murcaseId:""
      },
      header: {
        命案Id:"murcaseId",
        命案编号: "caseNo",
        //证件类型: "dicMap.cardCode",
        //证件号码: "cardNumber",
        姓名: "name",
        //曾用名: "oldName",
        性别: "dicMap.sex",
        出生日期: "birthDate",
        "国籍/地区": "dicMap.nationality",
        //民族: "dicMap.nation",
        籍贯: "dicMap.nativePlace",
        //婚姻状况: "dicMap.marriage",
        //政治面貌: "dicMap.politicsStatus",
        学历: "dicMap.education"
        //宗教信仰: "dicMap.religiousBelief",
        //职业类别: "dicMap.occupationType",
        //职业: "occupationDesc",
        //服务处所: "servicePlace",
        //户籍地: "domicilePlace",
        //户籍详址: "domicilePlaceDatail",
        //现住地: "livePlace",
        //现住详址: "livePlaceDetail",
        //是否为严重精神病患者: "dicMap.isInsanity",
        //是否为未成年人: "dicMap.isNonage",
        //是否为青少年: "dicMap.isTeenager",
      },
      data: [],
      recordCount: 0,
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "suspect"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
    service.requestCaseInfoByCode(this.queryForm.districtCode, data => {
      this.caseListOption = data.list;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    },
    "queryForm.districtCode": {
      handler(code) {
        service.requestCaseInfoByCode(code, data => {
          this.caseListOption = data.list;
        });
      }
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加命案犯罪嫌疑人 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
      this.$refs["vAddDialog"].caseListOption = this.caseListOption;
    },
    /** 删除命案犯罪嫌疑人 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteSuspect(item.id, data => {
            count == tableSelection.length - 1 && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "命案犯罪嫌疑人删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "命案犯罪嫌疑人删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
       pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageSuspect(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageSuspect(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              if (item.dicMap) {
                item.dicMap.isInsanity = item.isInsanity == 1 ? "是" : "否";
                item.dicMap.isNonage = item.isNonage == 1 ? "是" : "否";
                item.dicMap.isTeenager = item.isTeenager == 1 ? "是" : "否";
              }
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            if (item.dicMap) {
              item.dicMap.isInsanity = item.isInsanity == 1 ? "是" : "否";
              item.dicMap.isNonage = item.isNonage == 1 ? "是" : "否";
              item.dicMap.isTeenager = item.isTeenager == 1 ? "是" : "否";
            }
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>
